/**
* 法规库-执法领域组件
* @module 案件
* @desc 修改添加
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <el-dialog :close-on-click-modal='false' :title="title" :visible="show" @close="closeDialog" center top='30px'
                   width="650px">

            <el-form :model="form" :rules="rules" label-width="100px" ref="ruleForm" v-loading="loading"
                     size="medium">
                <el-form-item label="领域名称：" prop="enforceName">
                    <el-input placeholder="请输入领域名称" v-model="form.enforceName"></el-input>
                </el-form-item>
                <el-form-item label="领域编码：" prop="enforceCode">
                    <el-input placeholder="请输入编码" v-model="form.enforceCode"></el-input>
                </el-form-item>
                <el-form-item label="领域编号：" prop="enforceNum">
                    <el-input placeholder="请输入领域编号" v-model="form.enforceNum"></el-input>
                </el-form-item>
                <el-form-item label="职权依据：" prop="enforceRight">
                    <el-input :autosize="{minRows:4}" placeholder="请输入职权依据" type="textarea"
                              v-model="form.enforceRight"></el-input>
                </el-form-item>
                <el-form-item label="序号:">
                    <el-input-number :min="0" v-model="form.enforceOrder"></el-input-number>
                </el-form-item>
                <el-form-item label="是否启用:">
                    <el-switch :active-value="1" :inactive-value="0" v-model="form.isDisable"></el-switch>
                </el-form-item>
            </el-form>
            <span class="dialog-footer" slot="footer">
                <el-button @click="closeDialog" size="small" type="info">
                    <i class="iconfont icon-back"></i>取 消
                </el-button>
                <el-button @click="submitForm('ruleForm')" size="small" type="primary">
                    <i class="iconfont icon-save"></i>确 定
                </el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    import {apiLefAdd, apiLefDetailGet, apiLefUpdate} from "../../../../api/case";

    export default {
        name: 'edit',
        props: {
            show: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: '编辑'
            },
            type: {
                type: String,
                default: ''
            },
        },
        data() {
            return {
                loading:true,
                form: {},
                rules: {
                    enforceName: [
                        {required: true, message: '请输入领域名称', trigger: 'blur'},
                    ],
                    enforceName: [
                        {required: true, message: '请输入案由编号', trigger: 'blur'},
                    ],
                    enforceName: [
                        {required: true, message: '请输入处罚事项编号', trigger: 'blur'},
                    ],

                    enforceName: [
                        {required: true, message: '请输入领域编号', trigger: 'blur'},
                    ],
                    enforceCode: [
                        {required: true, message: '请输入领域编码', trigger: 'blur'},
                    ],
                    enforceRight: [
                        {required: true, message: '请输入职权依据', trigger: 'blur'},
                    ],
                }

            }
        },
        methods: {
            closeDialog() {
                this.$emit('update:show', false)
            },
            submitForm() {
                this.$refs.ruleForm.validate((valid) => {
                    if (valid) {
                        if (this.type) {
                            //编辑
                            apiLefUpdate(this.form).then(res => {
                                this.closeDialog();
                                this.$emit("getList")
                                this.$message({
                                    type: 'success',
                                    message: '修改成功!'
                                });
                            })
                        } else {
                            //添加
                            apiLefAdd(this.form).then(res => {
                                this.$emit("getList")
                                this.closeDialog();
                                this.$message({
                                    type: 'success',
                                    message: '添加成功!'
                                });
                            })
                        }
                    }
                });
            },
            //弹框弹出时获取data给form
            initPage() {
                if (this.type) {
                    apiLefDetailGet(this.type).then(res => {
                        this.loading = false;
                        this.form = res.data;
                    })
                } else {
                    this.loading = false;
                    this.form = {
                        isDisable: 1,
                        enforceOrder: 0
                    };
                }

            }
        },
        //监听，如果show为true,则执行initPage()
        watch: {
            'show'(value) {
                if (value) {
                    this.initPage();
                    this.$nextTick(() => {
                        this.$refs.ruleForm.clearValidate();
                    })
                }
            }
        },
    }
</script>
<style lang='less' scoped>
    .alert_wrop {
        padding: 10px;
        display: flex;
        justify-content: space-between;

    }
</style>


