/**
* 法规库-执法领域组件
* @module 案件
* @desc 查看
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <el-dialog :close-on-click-modal='false' :title="title" :visible="show" @close="closeDialog" width="750px"
    >
        <div class="detail-block" v-if="form" v-loading="loading">
            <h3>{{form.enforceName}}</h3>
            <div class="enforceCode"><span>编号：{{form.enforceCode}} <span> & </span>编码：{{form.enforceNum}}</span></div>
            <el-divider content-position="left">
                <div class="disabled">状态：{{ form.isDisable | disable}}</div>
            </el-divider>
            <div class="enforceRight-block">
                职权依据：
                <div class="enforceRight">{{form.enforceRight}}</div>
            </div>
            <el-divider content-position="right">
                <div class="disabled">创建时间：{{ form.createTime | formatDate}}</div>
            </el-divider>
        </div>
    </el-dialog>

</template>

<script>
    import {formatDate} from "../../../../until/wordBook";
    import {apiLefDetailGet} from "../../../../api/case";

    export default {
        name: "seeContent",
        props: {
            editId: {
                type: String,
                default: ''
            },
            show: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: ""
            },

        },
        data() {
            return {
                loading:true,
                form: '',

            }
        },
        methods: {
            closeDialog() {
                this.$emit('update:show', false)
            },
            //弹框弹出时获取data给form
            initPage() {
                apiLefDetailGet(this.editId).then(res => {
                    this.loading=false;
                    this.form = res.data;
                })
            }
        },
        filters: {
            disable(val) {
                return val == 1 ? '启用' : '停用'
            },
            formatDate(val) {
                return formatDate(val, 'yyyy-MM-dd hh:mm:ss')
            }

        },
        //监听，如果show为true,则执行initPage()
        watch: {
            'show'(value) {
                if (value) {
                    this.initPage();

                }
            }
        },
    }
</script>

<style lang="less" scoped>
    .concss {
        font-weight: bold
    }

    .detail-block {
        font-size:  @fontSize  + 4;

        h3 {
            text-align: center;
        }

        .disabled {
            color: #aaaaaa;
            font-size:  @fontSize + 2;
        }

        .enforceCode {
            text-align: center;
            color: #aaaaaa;
            font-size: @fontSize ;
        }

        .enforceRight-block {
            padding: 0 20px 20px;

            .enforceRight {
                text-indent: 2em;
                margin: 10px 0;
            }
        }

        .isDisable {
            padding: 0 20px;
            font-size:  @fontSize ;
            color: #888;
        }
    }
</style>
