<template>
    <el-container style="padding: 0">
        <el-aside class="tree-block" width="200px">
            <el-tree
                    :default-expanded-keys="['main']"
                    :load="onOpen"
                    :props="{label:'name',isLeaf:'isLeaf'}"
                    lazy
                    node-key="id" ref="tree">
                <div class="noFull" slot-scope="{ node, data }">
                    <div :class="{'item-block':true,active:node.data.id === mainObj.id}" @click.stop="onClick(node)">{{
                        node.label }}
                    </div>
                </div>
            </el-tree>
        </el-aside>
        <el-main style="padding: 10px;">
            <el-breadcrumb separator-class="el-icon-arrow-right" style="margin: 0 0 10px 0;color: #888888">
                <el-breadcrumb-item>法规案由库</el-breadcrumb-item>
                <el-breadcrumb-item v-if="mainObj.type > 0">执法领域</el-breadcrumb-item>
                <el-breadcrumb-item v-if="mainObj.type > 1">法律法规</el-breadcrumb-item>
                <el-breadcrumb-item v-if="mainObj.type > 2">处罚事项</el-breadcrumb-item>
                <el-breadcrumb-item v-if="mainObj.type > 3">案由&裁量</el-breadcrumb-item>
            </el-breadcrumb>
            <law-scope @upDateTree="upDateTree" v-if="mainObj.type === 1"/>
            <law-regulations :enforceId="mainObj.id" @upDateTree="upDateTree" v-if="mainObj.type === 2"/>
            <punish-block :lawId="mainObj.id" :tid="mainObj.tid" :version="mainObj.version" @upDateTree="upDateTree" v-if="mainObj.type === 3"/>
            <div v-if="mainObj.type === 4">
                <el-tabs type="card" v-model="activeName">
                    <el-tab-pane label="案由" name="first">
                        <cause-block :pmId="mainObj.id" :tid="mainObj.tid" :lawVersion='mainObj.lawVersion'/>
                    </el-tab-pane>
                    <el-tab-pane label="裁量基准" name="second">
                        <discretion :pmId="mainObj.id" :tid="mainObj.tid"/>
                    </el-tab-pane>
                </el-tabs>
            </div>
        </el-main>
    </el-container>
</template>

<script>
    import {apiLawSearch, apiLefLisGett, apiPunishListSearch} from "../../../api/case";
    import lawScope from './lawScope/index'; // 执法领域
    import lawRegulations from './law/index' // 法律法规
    import punishBlock from './punish' // 处罚事项
    import causeBlock from './cause' // 案由
    import discretion from './discretion' // 裁量基准
    export default {
        name: "lawSetPage",
        data() {
            return {
                activeName: 'first',
                mainObj: {
                    type: 1,
                    id: 'main',
                    tid:''
                }
            }
        },
        components: {lawScope, lawRegulations, punishBlock, causeBlock, discretion},
        methods: {
            initPage() {
                apiLefLisGett({pageNum: 1, pageSize: 1000}).then(res => {
                    res.data.forEach(i => {
                        i.children = [];
                        i.name = i.enforceName
                    });
                    this.list[0].children = res.data
                })
            },
            upDateTree(data) {
                this.$refs.tree.updateKeyChildren(this.mainObj.id, data)
            },
            onClick(data) {
                if (data.level === 1) {
                    this.mainObj.id = 'main'
                } else if (data.level === 2) {
                    this.mainObj.id = data.data.enforceId
                } else if (data.level === 3) {
                    this.mainObj.id = data.data.lawId;
                    this.mainObj.tid = data.data.clId;
                    this.mainObj.version = data.data.version;

                } else if (data.level === 4) {
                    this.mainObj.id = data.data.pmId;
                    this.mainObj.tid = data.data.tid;
                    this.mainObj.lawVersion = data.data.version;
                }
                this.mainObj.type = data.level;
            },
            onOpen(node, resolve) {
                if (node.level === 0) {
                    return resolve([{name: '法规案由库', id: 'main'}]);
                } else if (node.level === 1) {
                    apiLefLisGett({pageNum: 1, pageSize: 1000}).then(res => {
                        res.data.forEach(i => {
                            i.name = i.enforceName;
                            i.id = i.enforceId
                        });
                        return resolve(res.data);
                    })
                } else if (node.level === 2) {
                    let enforceId = node.data.enforceId;
                    let json = {pageSize: 1000, pageNum: 1, enforceId: enforceId, isEffective: 1};
                    apiLawSearch(json).then(res => {
                        res.data.forEach(i => {
                            i.name = i.lawName;
                            i.id = i.lawId
                        });
                        return resolve(res.data);
                    })
                } else if (node.level === 3) {
                    let lawId = node.data.lawId;
                    let json = {pageSize: 1000, pageNum: 1, lawId: lawId, isEffective: 1};
                    apiPunishListSearch(json).then(res => {
                        res.data.forEach(i => {
                            i.name = i.pmName;
                            i.id = i.pmId;
                            i.isLeaf = true
                        });
                        return resolve(res.data);
                    })
                }
            },
        },
        created() {
            // this.initPage()
        }
    }
</script>

<style lang="less" scoped>
    /deep/ .el-breadcrumb__inner {
        color: #888888;
    }

    .item-block {
        font-size:  @fontSize + 2;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;

        &.active {
            color: #6d8eff;
            font-weight: bold;
        }

        &:hover {
            color: #6d8eff;
            text-decoration: underline;
        }
    }

    .tree-block {
        background: #e7eef7;
        padding: 5px;

        .el-tree {
            background: #e7eef7;
        }
    }
</style>
