/**
* 法规库-执法领域组件
* @module 案件
* @desc 列表
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <div class="wrop-search">
            <lj-search-from :onAdd="onAdd" :searchObj="searchObj" @searchList="searchList"/>
        </div>
        <!-- 表格 -->
        <el-table :data="list" :header-cell-style="{background:'#e9eff8'}" :row-style="{cursor:'pointer'}"
                  @row-click="seeFunction" border v-loading="loading"
                  size="mini">
            <el-table-column align="center" label="编号" prop="enforceNum" width="150"/>
            <el-table-column align="center" label="名称" prop="enforceName" show-overflow-tooltip/>
            <!-- <el-table-column align="center" label="职权依据" prop="enforceRight" show-overflow-tooltip/> -->
            <el-table-column align="center" label="是否启用" prop="isDisable" width="100">
                <template slot-scope="scope">
                    {{scope.row.isDisable | YESORNO}}
                </template>
            </el-table-column>
            <el-table-column align="center" label="操作" width="150">
                <template slot-scope="scope">
                    <el-button @click.stop="editFunction(scope.$index, scope.row)" plain size="mini" type="warning"
                               v-if="buttonCheck('edit')">编辑
                    </el-button>
                    <el-button @click.stop="delateFunction(scope.$index, scope.row)" plain size="mini" type="danger"
                               v-if="buttonCheck('delete')">
                        删除
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <lj-pagination :pageNum.sync="form.pageNum" :pageSize.sync="form.pageSize" :total="total"
                       @getData="getList"
                       class="page-css"/>

        <!-- 编辑&添加弹框 -->
        <change-edit :show.sync="editObj.show" :title="editObj.title" :type="editObj.editId" @getList="getList"/>
        <!-- 查看 -->
        <see-content :editId='seeObj.editId' :show.sync='seeObj.show' :title='seeObj.title'></see-content>
    </div>
</template>

<script>
    import LjPagination from '../../../../components/LjPagination'
    import LjSearchFrom from '../../../../components/LjSearchForm'
    import {YESORNO} from "../../../../until/wordBook";
    import changeEdit from "./editadd";
    import seeContent from "./seeContent";
    import {apiLefDelete, apiLefLisGett} from "../../../../api/case";

    export default {
        name: "lawScopePage",
        data() {
            return {
                loading:true,
                //弹出框数据
                editObj: {
                    show: false,
                    title: '',
                    editId: null
                },
                seeObj: {
                    show: false,
                    title: '',
                    editId: null
                },

                //搜索组件数据
                searchObj: null,
                form: {
                    pageSize: 20,
                    pageNum: 1,
                },
                total: 0,
                list: [],
            }
        },
        components: {
            changeEdit, LjPagination, LjSearchFrom, seeContent
        },
        props: ['upDateTree'],

        methods: {
            //查询+搜索
            getList() {
                apiLefLisGett(this.form).then(res => {
                    this.total = res.total;
                    this.list = JSON.parse(JSON.stringify(res.data));
                    let arr = [];
                    res.data.forEach(i => {
                        i.name = i.enforceName;
                        i.id = i.enforceId;
                        arr.push(i)
                    });
                    this.$emit('upDateTree', arr);
                    this.loading=false;
                })
            },
            //新增
            onAdd() {
                this.editObj.editId = null;
                this.editObj.title = '新增执法领域';
                this.editObj.show = true;
            },
            //查看
            seeFunction(row) {
                this.seeObj.show = true;
                this.seeObj.title = '执法领域详情';
                this.seeObj.editId = row.enforceId;
            },
            //编辑
            editFunction(index, row) {
                this.editObj.editId = row.enforceId;
                this.editObj.title = '修改执法领域';
                this.editObj.show = true;
            },
            //删除按钮事件
            delateFunction(index, data) {
                this.$confirm('此操作将永久删除该领域, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    apiLefDelete(data.enforceId).then(res => {
                        this.getList();
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    })
                })
            },
            //搜索
            searchList(data) {  //data就是接收的this.form数据
                this.loading=true;
                data.pageSize = this.form.pageSize;
                this.form.pageNum = 1;
                // this.form = data;
                apiLefLisGett(data).then(res => {
                    this.list = res.data;
                    this.total = res.total;
                    let arr = [];
                    res.data.forEach(i => {
                        i.name = i.enforceName;
                        i.id = i.enforceId;
                        arr.push(i)
                    });
                    this.$emit('upDateTree', arr);
                    this.loading=false;
                })

            },

        },
        filters: {
            YESORNO(val) {
                return YESORNO[val]
            },
        },
        created() {
            this.getList();
        },
    }
</script>

<style scoped>
    .wrop-search {
        margin-bottom: 10px
    }

    .page-css {
        margin-top: 10px
    }
</style>
